<template>
  <div class="translate">
    <div class="head">
      <van-icon name="arrow-left" @click="back()"></van-icon>
      <div>自动检测语言</div>

    </div>

    <div class="inp">
      <van-field rows="2" id='inp' autosize type="textarea" maxlength="50" show-word-limit v-model="value" placeholder="在此输入要翻译的文本" />

    </div>
    <div class="res">
      <van-field rows="2" autosize type="textarea" @input="getTranslate()" v-model="result" readonly />
    </div>
    <h3 style="text-align:center;color:#ccc">按住说话</h3>
    <div style="display:flex;justify-content:center;">

      <div style="width:100px;height:100px;">
        <el-button id='btn' type=" info" style="width:100%;height:100%;border-radius:50%;background:#fff;border:2px solid #eee" @touchstart="touchstartHander()" @touchmove="touchmoveHander()" @touchend="touchendHander()"><i class="el-icon-microphone" id='mic' style="font-size:40px;color:#ccc"></i></el-button>
      </div>

    </div>

  </div>
</template>

<script>
  import { Button, Icon } from 'element-ui'
  import Vue from 'vue'
  import 'element-ui/lib/theme-chalk/index.css';
  Vue.use(Button)
  Vue.use(Icon)
  const Recorder = require('../assets/js/recorder.js')
  export default {
    name: 'Translate',
    data() {
      return {
        timer: null,
        value: '',
        result: '',
        websocket: null,
        username: window.sessionStorage.getItem('token'),
        opponentName: '',
        recorderxx: {},
        audio_context: null,
        intervalKey: null
      }
    },
    methods: {
      back() {
        this.$router.go(-1)
      },
      debounce(callback, wait) { //函数防抖：在事件被触发 n 秒后再执行回调，如果在这 n 秒内事件又被触发，则重新计时
        // 维护一个 timer，用来记录当前执行函数状态
        let timer = null
        return function () {
          // 通过 ‘this’ 和 ‘arguments’ 获取函数的作用域和变量
          let context = this,
            args = arguments
          if (timer) { // 如果此时存在定时器的话，则取消之前的定时器重新记时
            clearTimeout(timer)
            // timer=null
          }
          timer = setTimeout(function () { // 设置定时器，使事件间隔指定事件后执行
            callback.apply(context, args)
          }, wait)
        }
      },

      async getTranslate() {
        const { data: res } = await this.$http.get('/translate', {
          params: {
            word: this.value
          }
        })
        console.log('调用了翻译接口')
        this.result = res
      },

      //在方法里调用 this.websocketsend()发送数据给服务器
      start() {
        this.initWebSocket()
        navigator.mediaDevices.getUserMedia({ audio: true }).then((stream) => {
          var input = new AudioContext().createMediaStreamSource(stream);
          let xx;
          xx = new Recorder(input);
          this.recorderxx = xx;
          this.recorderxx.record();
        });

        console.log('握手成功');
      },
      sendData1() {
        var reader = new FileReader();
        this.recorderxx && this.recorderxx.exportWAV((blob) => {
          reader.onloadend = () => {
            var data = reader.result;
            var arr = new Int8Array(data);
            if (arr.length > 0) {
              var tmparr = new Int8Array(1024);
              var j = 0;
              for (var i = 0; i < arr.byteLength; i++) {
                tmparr[j++] = arr[i];
                if (((i + 1) % 1024) == 0) {
                  this.websocket.send(tmparr);

                  if (arr.byteLength - i - 1 >= 1024) {
                    tmparr = new Int8Array(1024);
                  } else {
                    tmparr = new Int8Array(arr.byteLength - i - 1);
                  }
                  j = 0;
                }
                if ((i + 1 == arr.byteLength) && ((i + 1) % 1024) != 0) {
                  this.websocket.send(tmparr);

                }
              }
            }
            var wzx = [1, 2, 3];
            tmparr = new Int8Array(wzx);
            this.websocket.send(tmparr);

          }
          reader.readAsArrayBuffer(blob); //这里拿到mp3格式的音频流写入到reader中
        })
      },

      stop() {
        this.recorderxx && this.recorderxx.stop();
        this.sendData1();
        this.recorderxx.clear();
      },
      initWebSocket() { // 初始化weosocket
        this.websocket = new WebSocket("ws://106.14.44.65:8889/send/" + this.username)
        this.websocket.onmessage = this.websocketonmessage
        this.websocket.onerror = this.websocketonerror
        this.websocket.onopen = this.websocketonopen
        this.websocket.onclose = this.websocketclose

      },
      websocketonopen() { // 连接建立之后执行send方法发送数据
        let data = {
          code: 0,
          msg: '这是client：初次连接'
        }
        // this.websocketsend(JSON.stringify(data))
      },
      websocketonerror() {
        console.log('WebSocket连接失败')
      },
      websocketonmessage(e) { // 数据接收
        console.log('数据接收' + e.data)
        this.value = e.data
        this.getTranslate()
      },

      websocketclose(e) { // 关闭
        console.log('已关闭连接', e)

      },


      touchstartHander(event) {
        // event.preventDefault();
        this.timer = setTimeout(this.LongPress, 500);
      },

      touchmoveHander(event) {
        event.preventDefault();
        clearTimeout(this.timer);
        console.log('move')
        this.timer = null;
        this.stop()
        document.querySelector('#mic').style.color = '#ccc'
      },

      touchendHander(event) {
        event.preventDefault();
        clearTimeout(this.timer);
        console.log('end')
        this.stop()
        document.querySelector('#mic').style.color = '#ccc'
        return false;
      },
      LongPress() {

        console.log("300S长按被触发");
        this.start()
        document.querySelector('#mic').style.color = '#66afb7'
      }
    },
    created() {
      console.log('created')

      // this.initWebSocket()
    },
    mounted() {
      var btn = document.querySelector('#btn')

      btn.addEventListener("touchstart", this.touchstartHander, false);
      btn.addEventListener("touchmove", this.touchmoveHander, false);
      btn.addEventListener("touchend", this.touchendHander, false);

      /// PC端
      btn.addEventListener("mousedown", this.touchstartHander, false);
      btn.addEventListener("mousemove", this.touchmoveHander, false);
      btn.addEventListener("mouseup", this.touchendHander, false);

      // 在 debounce 中包装我们的函数，过 2 秒触发一次
      document.getElementById('inp').addEventListener('input', this.debounce(this.getTranslate, 800))
    },

    destroyed() {
      this.websocket.close() // 离开路由之后断开websocket连接
    },




  }
</script>

<style lang="less" scoped>
  .translate {
    .head {
      background: linear-gradient(to top, #98eebc, 25%, #66afb7);
      display: flex;
      align-items: center;

      .van-icon {
        width: 10%;
        line-height: 54px;
        font-size: 25px;
        text-align: center;
      }
    }

    .inp,
    .res {

      padding: 10px;
      margin: 0 10px;

      .van-field,
      .result {
        min-height: 75px;
        border-radius: 10px;
        width: 100%;
        box-sizing: border-box;
        line-height: 30px;
        padding: 10px;
        border: 1px solid #eeeeee;
        background-color: rgba(248, 247, 247, 0.904);
      }
    }

    ul {
      list-style: none;
    }

    #recordingslist audio {
      display: block;
      margin-bottom: 10px;
    }
  }
</style>